import React, { useState, useEffect, useCallback, useContext } from 'react';
import ConversationSearch from '../ConversationSearch';
import ConversationListItem from '../ConversationListItem';
import Toolbar from '../Toolbar';
import ToolbarButton from '../ToolbarButton';
import { dataProvider, getTWToken } from '../../../components/authProvider'
import AddBoxIcon from '@material-ui/icons/AddBox';
import {
  Button
} from 'react-admin';
import NewConvoCreate from '../../../components/newCovoCreate'
import './ConversationList.css';
import { NotificationManager } from 'react-notifications';
import NotificationContext from '../../../components/Context'


const Conversations = require('@twilio/conversations');



export default function ConversationList(props) {


  const { notifications, setNotifications, removeAllMessNotif } = useContext(NotificationContext)
  const [conversations, setConversations] = useState([]);
  const [conversationHolder, setConverrsationHolder] = useState([]);
  const [searchText, setSearchText] = useState(null)
  const [newConvoVisible, setNewConvoVisible] = useState(false)


  useEffect(() => {
    getConversations()
  }, [])

  const notify = (event, conversation) => {
    let convoItem = conversation.filter(i => i.text === event.conversation.channelState.uniqueName.split(':')[1])[0]
    NotificationManager.info(convoItem.name, 'New message', 3000);
    setNotifications({ message: `Message from ${convoItem.name}`, type: 'message', convoItem:convoItem })

  }


  const refreshConvoList = useCallback(async (client, event) => {



    if (client) {

      let items = []
      async function processChannels(paginator) {
        if (paginator.hasNextPage) {

          const nextPaginator = await paginator.nextPage();
          items = items.concat(nextPaginator.items)
          await processChannels(nextPaginator);
        }
      }
      let clientconvo = await client.getSubscribedConversations()
      items = clientconvo.items
      await processChannels(clientconvo);
      let newConversations = await Promise.all(items.map(async result => {
        let client = await dataProvider.getList('clients', { filter: { 'phone': result.channelState.friendlyName.split(':')[1] }, pagination: { page: 1, perPage: 1 } })

        return {

          name: client.data[0] ? client.data[0].name : result.channelState.friendlyName.split(':')[1],
          text: client.data[0] ? client.data[0].phone : result.channelState.friendlyName.split(':')[1],
          dateUpdated: result.lastMessage ? result.lastMessage.dateCreated : result.dateUpdated,
          sid: result.sid,
          unreadCount: result.lastMessage ? result.lastMessage.index - result.channelState.lastReadMessageIndex : 0
        };
      }));
      newConversations.sort((a, b) => { return new Date(b.dateUpdated) - new Date(a.dateUpdated) })
      setConversations([...conversations, ...newConversations])
      setConverrsationHolder(newConversations)
      event && notify(event, newConversations)
    }

  }, [conversations])

  const refreshToken = async (client) => {


    console.log('token about to expire')
    let token = await getTWToken()
    client.updateToken(token);

    console.log('client\'s token updated')
  }

  async function getClient(token, callback) {
    let items = []
    async function processChannels(paginator) {


      if (paginator.hasNextPage) {

        const nextPaginator = await paginator.nextPage();
        items = items.concat(nextPaginator.items)
        await processChannels(nextPaginator);
      }
    }

    let client = await Conversations.Client.create(token);


    client.addListener('tokenAboutToExpire', () => refreshToken(client));

    client.addListener('connectionStateChanged', async (event) => {
      if (event === 'connected') {
        let clientconvo = await client.getSubscribedConversations()
        items = clientconvo.items
        await processChannels(clientconvo);
        props.setClient(client)

        if (client._eventsCount === 2) {
          client.addListener('messageAdded', (event) => refreshConvoList(client, event))
          client.addListener('conversationRemoved', () => refreshConvoList(client))
          client.addListener('conversationAdded', (event) => refreshConvoList(client, event))
        }
        callback(items)
      }
    })
  }

  const searchFilterFunction = (text) => {

    const newData = conversationHolder.filter(item => {


      let name
      try {
        name = item.name.toUpperCase()
      }
      catch { name = null }
      let phone
      try {
        phone = item.phone
      }
      catch { phone = null }

      const itemData = `${name} ${phone}`;
      const textData = text.toUpperCase();

      return itemData.indexOf(textData) > -1;
    });

    setSearchText(text)
    setConversations(newData)

  };


  const createConvo = async (number, name) => {

    let twiConvo = await props.client.createConversation({ "friendlyName": "pBjefCCX5JXw6QbYFu2GR7rmouw1:" + number, "uniqueName": "pBjefCCX5JXw6QbYFu2GR7rmouw1:" + number })
    await twiConvo.addNonChatParticipant("+61488844176", number)

    await twiConvo.add("pBjefCCX5JXw6QbYFu2GR7rmouw1")
    //refreshConvoList()// set this as event listener
    props.setactiveConvo({ sid: twiConvo.sid, name: name })


  }


  const getConversations = async () => {

    let token = await getTWToken()

    getClient(token, async convos => {


      let newConversations = await Promise.all(convos.map(async result => {
        let client = await dataProvider.getList('clients', { filter: { 'phone': result.channelState.friendlyName.split(':')[1] }, pagination: { page: 1, perPage: 1 } })

        return {

          name: client.data[0] ? client.data[0].name : result.channelState.friendlyName.split(':')[1],
          text: client.data[0] ? client.data[0].phone : result.channelState.friendlyName.split(':')[1],
          dateUpdated: result.lastMessage ? result.lastMessage.dateCreated : result.dateUpdated,
          sid: result.sid,
          unreadCount: result.lastMessage ? result.lastMessage.index - result.channelState.lastReadMessageIndex : 0
        };
      }));
      newConversations.sort((a, b) => { return new Date(b.dateUpdated) - new Date(a.dateUpdated) })
      setConversations([...conversations, ...newConversations])
      setConverrsationHolder(newConversations)

    })



  }

  const setactiveConvo = (obj) => {
    props.setactiveConvo(obj)
    removeAllMessNotif(obj)
    conversations[conversations.findIndex(i => i.sid === obj.sid)].unreadCount = 0
    conversationHolder[conversationHolder.findIndex(i => i.sid === obj.sid)].unreadCount = 0
    setConversations(conversations)
    setConverrsationHolder(conversationHolder)
  }

  return (
    <div className="conversation-list">
      <Toolbar
        title="Messenger"
        leftItems={[
          <ToolbarButton key="cog" icon="ion-ios-cog" />
        ]}
        rightItems={[
          <Button
            onClick={() => setNewConvoVisible(!newConvoVisible)}
          >
            <AddBoxIcon />
          </Button>,
          <ToolbarButton key="add" icon="ion-ios-add-circle-outline" />
        ]}
      />
      <ConversationSearch
        searchFilterFunction={searchFilterFunction}
        searchText={searchText}
      />
      {
        conversations.map(conversation =>
          <ConversationListItem
            key={conversation.sid}
            data={conversation}
            onClick={setactiveConvo}
          />
        )
      }
      <NewConvoCreate visible={newConvoVisible} open={setNewConvoVisible} activeConvos={conversations} setActiveConvo={props.setactiveConvo} createNewConvo={createConvo} />
    </div>
  );
}