import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import { useState, Fragment, useEffect } from 'react';
import {
    Button,
} from 'react-admin';
import IconCancel from '@material-ui/icons/Cancel';



import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { FixedSizeList } from 'react-window';
import ConversationSearch from '../../views/messenger/ConversationSearch'
import { getList } from '../../components/authProvider'


const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        height: 500,
        maxWidth: 300,
        backgroundColor: theme.palette.background.paper,
    },
}));




export default function NewConvoCreate(props) {

    const classes = useStyles();
    const [allCust, setAllCust] = useState([])
    const [allCustHolder, setAllCustHolder] = useState([])
    const [total, setTotal] = useState('')
    const [searchText, setSearchText] = useState('')

    useEffect(async () => {

        let all_clients = await getList('clients')
        setTotal(all_clients.total)
        setAllCust(all_clients.data)
        setAllCustHolder(all_clients.data)


    }, [])
    const searchFilterFunction = (text) => {

        const newData = allCustHolder.filter(item => {


            let name
            try {
                name = item.name.toUpperCase()
            }
            catch { name = null }


            const itemData = `${name}`;
            const textData = text.toUpperCase();

            return itemData.indexOf(textData) > -1;
        });

        setSearchText(text)
        setAllCust(newData)
        setTotal(newData.length)

    };

    const handleClick = (event) => {

        let search = props.activeConvos.filter(i => i.text === event.phone)
        if (search.length > 0) {

            props.setActiveConvo({ sid: search[0].sid, name: search[0].name })
            props.open(!props.visible)
        }
        else {

            props.createNewConvo(event.phone, event.name)
            props.open(!props.visible)
        }


    }



    function renderRow(props) {
        const { index, style } = props;

        return (
            <ListItem onClick={async () => handleClick(allCust[index])} button style={style} key={index} >
                <ListItemText primary={allCust[index].name} />
            </ListItem>
        );
    }

    renderRow.propTypes = {
        index: PropTypes.number.isRequired,
        style: PropTypes.object.isRequired,
    };

    return (
        <Fragment>
            <Dialog open={props.visible}>

                <DialogTitle>Create new Conversation</DialogTitle>

                <DialogContent>
                    <div className={classes.root}>
                        <ConversationSearch searchText={searchText} searchFilterFunction={searchFilterFunction} />
                        <FixedSizeList height={400} width={300} itemSize={46} itemCount={total} >
                            {renderRow}
                        </FixedSizeList>
                    </div>

                </DialogContent>

                <DialogActions>

                    <Button
                        label="Cancel"
                        onClick={() => props.open(!props.visible)}
                    >
                        <IconCancel />
                    </Button>
                </DialogActions>

            </Dialog>
        </Fragment>

    )
}


