import * as React from 'react';
import { Fragment, useState } from 'react';
import {



  useNotify,
  useUnselectAll,
  BulkDeleteButton,
  useGetIdentity,
  Button,
  Loading

} from 'react-admin';
import { makeStyles } from '@material-ui/core/styles';
import IconCancel from '@material-ui/icons/Cancel';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Input from '@material-ui/core/Input';
import SendIcon from '@material-ui/icons/Send';
import { getToken } from './authProvider'
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import { NotificationContainer, NotificationManager } from 'react-notifications';
var axios = require('axios');
var qs = require('qs');




const BookingBulk = ({ selectedIds }) => {


  const useStyles = makeStyles((theme) => ({
    root: {
      '& .MuiTextField-root': {
        margin: theme.spacing(1),
        width: '25ch',
      },
    },
  }));


  const [open, setOpen] = useState(false);
  const [open1, setOpen1] = useState(false);

  const notify = useNotify();
  const unselectAll = useUnselectAll();
  const { identity, loading: identityLoading } = useGetIdentity();
  const classes = useStyles();
  const [topic, setTopic] = React.useState();
  const [message, setMessage] = React.useState();
  const [amount, setAmount] = React.useState();
  const [loading, setLoading] = useState(false);

  const handleClick = () => setOpen(true);
  const handleChargeClick = () => setOpen1(true);
  const handleDialogClose = () => { setOpen(false); }
  const handleDialogClose1 = () => { setOpen1(false); }
  const handleConfirm = async () => {
    setLoading(true)
    let token = await getToken()


    var data = qs.stringify([token, selectedIds, 'bookings', topic, message]);

    var config = {
      method: 'post',
      url: 'https://7a314e3ed3bb.ngrok.io',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      data: data
    };

    axios(config)
      .then(function (res) {
        setLoading(false)
        setOpen(false);
        notify('Messages sent');
        unselectAll('bookings');
        setTopic(null)
        setMessage(null)

      })
      .catch(function (err) {
        setLoading(false)
        notify('Error: Could not reach server', 'warning')

      });


  };
  const handleConfirm1 = async () => {
    setLoading(true)
    let token = await getToken()


    var data = qs.stringify([token, selectedIds, amount]);

    var config = {
      method: 'post',
      url: 'https://a9548241041d.ngrok.io/talk',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      data: data
    };

    axios(config)
      .then(function (res) {
        setLoading(false)
        setOpen(false);
        notify('Messages sent');
        unselectAll('bookings');

        setAmount(null)

      })
      .catch(function (err) {
        setLoading(false)
        notify('Error: Could not reach server', 'warning')

      });


  };


  //        






  return (

    <Fragment>
      {selectedIds.length == 1 && <Button label="Charge Card" onClick={handleChargeClick} />}
      <Button label="Text clients" onClick={handleClick} />

      <Dialog


        open={open1}
        onClose={handleDialogClose1}
      >

        <DialogTitle>Charge Card on File</DialogTitle>

        <DialogContent>

          <Input name='Amount' fullWidth placeholder="Amount" defaultValue={amount && amount} onChange={event => { setAmount(event.target.value) }} />


        </DialogContent>
        <DialogActions>



          <Button
            label="Cancel"
            onClick={handleDialogClose1}

          >
            <IconCancel />
          </Button>
          <Button
            label="Charge"
            onClick={handleConfirm1}

          >
            <MonetizationOnIcon />
          </Button>
        </DialogActions>

      </Dialog>

      <Dialog


        open={open}
        onClose={handleDialogClose}
      >

        <DialogTitle>Message Owners</DialogTitle>

        <DialogContent>

          <Input name='Topic' fullWidth placeholder="Topic" defaultValue={topic && topic} onChange={event => { setTopic(event.target.value) }} />
          <Input name='Message' multiline fullWidth placeholder='Message' defaultValue={message && message} onChange={event => { setMessage(event.target.value) }} />

        </DialogContent>
        {loading === false &&
          <DialogActions>



            <Button
              label="Cancel"
              onClick={handleDialogClose}

            >
              <IconCancel />
            </Button>
            <Button
              label="Send"
              onClick={handleConfirm}

            >
              <SendIcon />
            </Button>
          </DialogActions>}

      </Dialog>
      <BulkDeleteButton />
    </Fragment>

  );
}



export default BookingBulk;