import * as React from "react";
import {
    List,
    SimpleList,
    Datagrid,
    TextField,
    ReferenceField,
    EditButton,
    Edit,
    Create,
    SimpleForm,
    ReferenceInput,
    SelectInput,
    TextInput,
    Filter,
    Pagination,
    EmailField,
    BooleanField,
    BooleanInput,
    DateField, TabbedForm,
    FormTab,
    AutocompleteInput,
    DateInput,
    FilterList, FilterListItem,
    FilterLiveSearch,
    SelectField, ArrayField,
    ChipField,
    ReferenceArrayField,
    SingleFieldList,
    ReferenceManyField,
    SearchInput,
    ReferenceArrayInput

} from 'react-admin';
import { useMediaQuery, Card as MuiCard, CardContent, withStyles } from '@material-ui/core';
import CustomizableDatagrid from 'ra-customizable-datagrid';
import TextArrayField from '../fields/myChipField'
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import { MyClientRefField, MyServiceRef, MydogRef } from '../fields/myRefField'
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import BookingBulk from '../components/BookingBulk'

import { MytimeRange } from '../fields/myRefField'



const PostPagination = props => <Pagination rowsPerPageOptions={[50, 100, 200]} {...props} />;
const BookingFilter = (props) => (

    <Filter {...props}>
        <DateInput label="Booking Date" source="start" alwaysOn />
        <SearchInput source="name" placeholder='Search client' />
        <SearchInput source="dogName" placeholder='Search dog' />
        <ReferenceArrayInput
            label="Service"
            source="service_ref"
            reference="services"
            sort={{ field: 'name', order: 'ASC' }}
            filterToQuery={searchText => ({ service: searchText })}


        >
            <AutocompleteInput optionText={word => { return word.service + ' ' + word.category }} />
        </ReferenceArrayInput>
        <SearchInput source="code" placeholder='Search code' />

    </Filter>
);

const Card = withStyles(theme => ({
    root: {
        [theme.breakpoints.up('sm')]: {
            order: -1, // display on the left rather than on the right of the list
            width: '15em',
            marginRight: '1em',
        },
        [theme.breakpoints.down('sm')]: {
            display: 'none',
        },
    },
}))(MuiCard);

const CategoryFilter = () => (
    <FilterList
        label="Category"
        icon={<ControlPointIcon />}
    >
        <FilterListItem
            label="Daycare"
            value={{
                category: 'daycare'
            }}
        />
        <FilterListItem
            label="Grooming"
            value={{
                category: 'stylist'
            }}
        />
        <FilterListItem
            label="Coiffuriste"
            value={{
                category: 'coiffuriste'
            }}
        />


    </FilterList>
);


const StatusFilter = () => (
    <FilterList
        label="Status"
        icon={<ControlPointIcon />}
    >
        <FilterListItem
            label="Confirmed"
            value={{
                status: 'confirmed'
            }}
        />
        <FilterListItem
            label="Modified"
            value={{
                status: 'modified'
            }}
        />
        <FilterListItem
            label="Cancelled"
            value={{
                status: 'cancelled'
            }}
        />
    </FilterList>
);


const FilterSidebar = () => (
    <Card>
        <CardContent>
            <CategoryFilter />
            <StatusFilter />
        </CardContent>
    </Card>
);



export const BookingList = props => {

    const isSmall = useMediaQuery(theme => theme.breakpoints.down('sm'));
    return (
        <List aside={<FilterSidebar />} bulkActionButtons={<BookingBulk />} perPage={50} {...props} pagination={<PostPagination />} filters={<BookingFilter />} >{isSmall ? (
            <SimpleList
                primaryText={record => record.name}
                secondaryText={record => record.email}
                tertiaryText={record => record.phone}
            />
        ) : (


            <CustomizableDatagrid>
                <MyClientRefField label="Client" source="client_ref" reference="clients" />

                <MydogRef label="Dog Name" source='dog_ref' reference='dogs' />

                <MyServiceRef label="Service" source="service_ref" reference="services" />
                <DateField label='Booking Date' source="start" options={{ weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }} />
                <MytimeRange label='Time' />
                <TextField source="status" />

                <TextArrayField source="addOns">
                    <SingleFieldList>
                        <ChipField source="id" />
                    </SingleFieldList>
                </TextArrayField>

                <TextField source="comments" />



                <DateField source="updated" />
                <TextField source="code" />
                <EditButton />
            </CustomizableDatagrid>

        )}
        </List>

    );
}
