
var axios = require('axios');









export const SendPicToServ = async (token, file) => {



    function readFile(file) {
        return new Promise((resolve, reject) => {
            let fr = new FileReader();
            fr.onload = x => resolve(fr.result);
            fr.onerrror = reject;
            fr.readAsDataURL(file)
        })
    }

    let base64 = await readFile(file)

    var data = JSON.stringify({
        "token": token,
        "image": base64.replace("data:", "").replace(/^.*;base64,/, "")
    });

    var config = {
        method: 'post',
        url: 'https://australia-southeast1-admin-a4a00.cloudfunctions.net/adminPicUploader',
        headers: {
            'Authorization': 'Basic ODU2NzZCNUEtOEE5MS00NzYwLUE4MTctODQ2MUIyQ0I3MTI0OjZCQ0FGQUY5LUVFQ0YtNDRBNi04QTBBLTg3RjFDQUYxQTQxNg==',
            'Content-Type': 'application/json'
        },
        data: data
    };

    return axios(config)
        .then(function (response) {
            return response.data
        })
        .catch(function (error) {
            return error
        });


}


