import React, { useEffect, useState, useRef, useCallback } from 'react';
import Compose from '../Compose';
import Toolbar from '../Toolbar';
import ToolbarButton from '../ToolbarButton';
import Message from '../Message';
import moment from 'moment';
import { SendPicToServ } from '../../../components/TwilioHelper'
import { getToken } from '../../../components/authProvider'
import './MessageList.css';


const MY_USER_ID = 'pBjefCCX5JXw6QbYFu2GR7rmouw1';

export default function MessageList(props) {

  const [messages, setMessages] = useState([])
  const [newMessage, setNewMessage] = useState(null)
  const [convo, setConvo] = useState(null)
  const messagesEndRef = useRef(null)
  const [selectedFile, setSelectedFile] = useState(null)

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }




  const onPicSend = async () => {
    let token = await getToken()

    for (let file of selectedFile) {
      let url = await SendPicToServ(token, file)

      await sendMessage(url)
    }
  }

  useEffect(() => {
    scrollToBottom()
  }, [messages]);
  const getMessages = useCallback( async function  () {
    var tempMessages = []
    if (props.activeConvo) {


       let convo = await props.client.getConversationBySid(props.activeConvo.sid)
       convo._events.messageAdded.length === 0 && convo.addListener('messageAdded', getMessages)
      
       convo.channelState.lastMessage && convo.advanceLastReadMessageIndex(convo.channelState.lastMessage.index)
       setConvo(convo)
      let messages = await convo.getMessages()
      tempMessages = messages.items.map(i => { return { id: i.state.index, author: i.state.author, message: i.state.body, timestamp: i.state.timestamp } })

    }

    setMessages(tempMessages)
  },[props.activeConvo, props.client])

  useEffect(() => {

    getMessages();
    return ()=>{
      if (convo){
        convo.removeListener('messageAdded', getMessages)
        
      }
    }
  }, [convo, getMessages, props.activeConvo, props.client])


  

  const renderMessages = () => {
    let i = 0;
    let messageCount = messages.length;
    let tempMessages = [];

    while (i < messageCount) {
      let previous = messages[i - 1];
      let current = messages[i];
      let next = messages[i + 1];
      let isMine = current.author === MY_USER_ID;
      let currentMoment = moment(current.timestamp);
      let prevBySameAuthor = false;
      let nextBySameAuthor = false;
      let startsSequence = true;
      let endsSequence = true;
      let showTimestamp = true;

      if (previous) {
        let previousMoment = moment(previous.timestamp);
        let previousDuration = moment.duration(currentMoment.diff(previousMoment));
        prevBySameAuthor = previous.author === current.author;

        if (prevBySameAuthor && previousDuration.as('hours') < 1) {
          startsSequence = false;
        }

        if (previousDuration.as('hours') < 1) {
          showTimestamp = false;
        }
      }

      if (next) {
        let nextMoment = moment(next.timestamp);
        let nextDuration = moment.duration(nextMoment.diff(currentMoment));
        nextBySameAuthor = next.author === current.author;

        if (nextBySameAuthor && nextDuration.as('hours') < 1) {
          endsSequence = false;
        }
      }

      tempMessages.push(
        <Message
          key={i}
          isMine={isMine}
          startsSequence={startsSequence}
          endsSequence={endsSequence}
          showTimestamp={showTimestamp}
          data={current}
        />
      );

      // Proceed to the next message.
      i += 1;
    }

    return tempMessages;
  }

  const sendMessage = async (img) => {
    await convo.sendMessage(img ? img : newMessage)
    let messages = await convo.getMessages()
    let tempMessages = messages.items.map(i => { return { id: i.state.index, author: i.state.author, message: i.state.body, timestamp: i.state.timestamp } })
    setNewMessage("")
    setMessages(tempMessages)
  }




  return (
    <div className="message-list">
      <Toolbar
        title={props.activeConvo ? props.activeConvo.name : 'Select a conversation'}
        rightItems={[
          <ToolbarButton key="info" icon="ion-ios-information-circle-outline" />,
          <ToolbarButton key="video" icon="ion-ios-videocam" />,
          <ToolbarButton key="phone" icon="ion-ios-call" />
        ]}
      />

      <div className="message-list-container">{renderMessages()}
        <div ref={messagesEndRef} />
      </div>


      <Compose
        value={newMessage}
        onChange={setNewMessage}
        onSend={sendMessage}
        onPicSend={onPicSend}
        onDrop={setSelectedFile}
        rightItems={[

          <ToolbarButton key="photo" icon="ion-ios-camera" />,
          <ToolbarButton key="image" icon="ion-ios-image" />,
          <ToolbarButton key="audio" icon="ion-ios-mic" />,
          <ToolbarButton key="money" icon="ion-ios-card" />,
          <ToolbarButton key="games" icon="ion-logo-game-controller-b" />,
          <ToolbarButton key="emoji" icon="ion-ios-happy" />
        ]} />
    </div>
  );
}