import * as React from 'react';
import { Fragment, useState } from 'react';
import {



    useNotify,
    useUnselectAll,
    BulkDeleteButton,
    useGetIdentity,
    Button,
    Loading

} from 'react-admin';
import { makeStyles } from '@material-ui/core/styles';
import IconCancel from '@material-ui/icons/Cancel';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Input from '@material-ui/core/Input';
import SendIcon from '@material-ui/icons/Send';
import {getToken} from './authProvider'

var axios = require('axios');
var qs = require('qs');



const DogBulk =  ({ selectedIds }) => {


    const useStyles = makeStyles((theme) => ({
        root: {
          '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '25ch',
          },
        },
      }));    


    const [open, setOpen] = useState(false);

    const notify = useNotify();
    const unselectAll = useUnselectAll();
    const { identity, loading: identityLoading } = useGetIdentity();
    const classes = useStyles();
    const [topic, setTopic] = React.useState();
    const [message, setMessage] = React.useState();
    const [loading, setLoading] = useState(false);
   
    const handleClick = () => setOpen(true);
    const handleDialogClose =  () => { setOpen(false);}

    const handleConfirm = async () => {
      setLoading(true)
      let token = await getToken()
    

        var data = qs.stringify([token,selectedIds,'dogs', topic, message]);

        var config = {
          method: 'post',
          url: 'https://7a314e3ed3bb.ngrok.io',
          headers: { 
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          data : data
        };
        
        axios(config)
        .then(function (res) {
          setLoading(false)
          setOpen(false);
          notify('Messages sent');
          unselectAll('dogs');
          setTopic(null)
          setMessage(null)

        })
        .catch(function (err) {
          setLoading(false)
          notify('Error: Could not reach server', 'warning')

        });


    };

//        






    return (
        <Fragment>
            <Button label="Text clients" onClick={handleClick}  />

            <Dialog

                  
                    open={open}
                    onClose={handleDialogClose}
                >
                    
                    <DialogTitle>Message Owners</DialogTitle>
                   
                      <DialogContent> 

                        <Input name='Topic' fullWidth placeholder="Topic" defaultValue={topic && topic } onChange={event =>{setTopic(event.target.value)}}  />
                        <Input name='Message' multiline fullWidth placeholder='Message' defaultValue={message && message } onChange={event =>{setMessage(event.target.value)}}  />

                      </DialogContent>
                      {loading===false &&
                      <DialogActions>
                 
   
                 
                        <Button
                            label="Cancel"
                            onClick={handleDialogClose}
                
                        >
                            <IconCancel />
                        </Button>
                  <Button
                            label="Send"
                            onClick={handleConfirm}
                            
                        >
                            <SendIcon />
                        </Button>
                    </DialogActions>}
                    
                    </Dialog>
        <BulkDeleteButton  />
        </Fragment>
        
    );
}

 

export default DogBulk;