import React, { useEffect } from 'react';
import shave from 'shave';


import './ConversationListItem.css';



export default function ConversationListItem(props) {
  useEffect(() => {
    shave('.conversation-snippet', 20);
  })


  const { photo, name, text, unreadCount } = props.data;

  return (
    <div className="conversation-list-item" onClick={() => {

      props.onClick({ sid: props.data.sid, name: props.data.name })
    }}>

      <div className="conversation-info">
        <h1 className="conversation-title">{name}
        </h1>
        <p className="conversation-snippet">{text}
        {unreadCount !== 0 && <text className="badge">{unreadCount}</text>}
        </p>

      </div>
    </div>
  );
}