
import { linkToRecord } from 'react-admin';
import { Link } from 'react-router-dom';


export const MyClientRefField = ({ record, source }) => {

  MyClientRefField.defaultProps = {filterBy : record.name}

    if (record[source]) {
        const linkToUser = linkToRecord('/clients', record[source], ); //'show'
      
    

        return ( <Link to={linkToUser}>{record.name}</Link>


       )
    } else {
      return (
     
        <span >{record.name}</span>
     
      )    
    }

    
}
export const MyServiceRef = ({ record, source }) => {



    if (record[source]) {
        const linkToService = linkToRecord('/services', record[source]);
      
    

        return ( <Link to={linkToService}>{record.service + ' ' + record.category}</Link>


       )
    } else {
      return (
     
        <span >{record.service + ' ' + record.category}</span>
     
      )    
    }
  
    
}


export const MydogRef = ({ record, source }) => {

  

    if (record[source]) {
        const linkToDogs = linkToRecord('/dogs', record[source]);
      
    

        return ( <Link to={linkToDogs}>{record.dogName}</Link>


       )
    } else if (!record[source]) {
      return (
     
        <span >{record.dogName}</span>
     
      )    
    }
    
}

export const MytimeRange = ({ record }) => {



    return (
   
      <span >{new Date (record.start).toLocaleString().split(',')[1].trim() + ' - ' + new Date (record.end).toLocaleString().split(',')[1].trim()}</span>
   
    )    
  
  
}



    