import React from "react";
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Badge from '@material-ui/core/Badge';
import NotificationContext from './Context'
import EmailIcon from '@material-ui/icons/Email';
import ContactlessIcon from '@material-ui/icons/Contactless';
import { useHistory } from "react-router-dom";

export default function Notifis(props) {

  const { notifications,  removeNotif } = React.useContext(NotificationContext)

  const [anchorEl, setAnchorEl] = React.useState(null);

  const history = useHistory();


  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const onItemClick = (item) => {

    removeNotif(item)
    history.push('/messenger');

  }

  const IconSelector = (item) => {
    if (item.type === 'payment') {
      return (<ListItemIcon >
        <ContactlessIcon fontSize="small" />
      </ListItemIcon>)
    }
    else if (item.type === 'message') {
      return (<ListItemIcon >
        <EmailIcon fontSize="small" />
      </ListItemIcon>)
    }
  }


  return (
    <div>
      <Badge style={{
        height: "25px",
        width: "45px"
      }} badgeContent={notifications.length} color="error">
        <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
          <NotificationsIcon htmlColor='white' />
        </Button>
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >{notifications.length > 0 ? notifications.map(i => <MenuItem onClick={() => { onItemClick(i) }}>{IconSelector(i)}{i.message}</MenuItem>) :
          <MenuItem disabled>All Caught Up</MenuItem>
          }

        </Menu>
      </Badge>
    </div>
  );
}

 