import React from "react";


const NotificationContext = React.createContext();

export const ContextConsumer = NotificationContext.Consumer



class ContextProvider extends React.Component {
    state = {
        notifications: []
    }


    setNotifications = (notifi) => {
        let notif = this.state.notifications
        notif.push(notifi)
        this.setState({ notifications: notif })
    }

    removeNotif = (notif) => {
        this.setState({notifications: this.state.notifications.filter(i => i !== notif)})
    }

    removeAllMessNotif = (notif) => {
        
        this.setState({notifications: this.state.notifications.filter(i => i.convoItem.sid !== notif.sid)})
    }

    render() {
        const { children } = this.props
        const { notifications } = this.state
        const { setNotifications, removeNotif, removeAllMessNotif } = this

        return (
            <NotificationContext.Provider
                value={{
                    notifications, setNotifications, removeNotif,
                    removeAllMessNotif
                }}
            >
                {children}
            </NotificationContext.Provider>
        );

    }


}


export { ContextProvider }
export default NotificationContext