import * as React from "react";
import {
    List,
    SimpleList,
    Datagrid,
    TextField,
    ReferenceField,
    EditButton,
    Edit,
    Create,
    SimpleForm,
    ReferenceInput,
    SelectInput,
    TextInput,
    Filter,
    Pagination,
    EmailField,
    BooleanField,
    BooleanInput,
    DateField,TabbedForm,
    FormTab,
    AutocompleteInput,
    DateInput,
    FilterList, FilterListItem,
    FilterLiveSearch,
    SelectField
  
} from 'react-admin';
import { useMediaQuery,  Card as MuiCard, CardContent, withStyles  } from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOnOutlined';
import AccessTimeIcon from '@material-ui/icons/AccessTime';

import DogBulk from '../components/dogsBulk'
import CustomizableDatagrid from 'ra-customizable-datagrid';




const PostPagination = props => <Pagination rowsPerPageOptions={[50, 100, 200]} {...props} />;


const PostFilter = (props) => (
    <Filter {...props}>
        <TextInput label="Search" source="q" alwaysOn />
        <ReferenceInput label="Owner" source="owner" reference="users" allowEmpty>
            <SelectInput optionText="name" />
        </ReferenceInput>
    </Filter>
);



const MaleFemaleFilter = () => (
    <FilterList label="Sex" icon={<AccessTimeIcon />}>

    
        <FilterListItem
            label="Male"
            value={{
                sex: "M"
            }}
        />
         <FilterListItem
            label="Female"
            value={{
               sex: 'F'
  
            }}
        />
        <FilterListItem
            label="Desexed"
            value={{
               desexed: true
  
            }}
        />
    </FilterList>
);

const Card = withStyles(theme => ({
    root: {
        [theme.breakpoints.up('sm')]: {
            order: -1, // display on the left rather than on the right of the list
            width: '15em',
            marginRight: '1em',
        },
        [theme.breakpoints.down('sm')]: {
            display: 'none',
        },
    },
}))(MuiCard);

const FilterSidebar = () => (
    <Card>
        <CardContent>
        <FilterLiveSearch source="dog_name" />

            <MaleFemaleFilter />
        </CardContent>
    </Card>
);

export const DogList = (props) => {
    const isSmall = useMediaQuery(theme => theme.breakpoints.down('sm'));
    return (
        <List aside={<FilterSidebar />} perPage={50} pagination={<PostPagination />}  bulkActionButtons={<DogBulk />}  {...props}>
         
            {isSmall ? (
                <SimpleList
                    primaryText={record => record.dog_name}
                    secondaryText={record =>record.Client}
                />
            ) : (
                
                <CustomizableDatagrid>
                      <TextField source="dog_name" label='Dog Name' />
                    <ReferenceField label="Client" source="owner" reference="clients"  >
                        <TextField source="name"  />
                    </ReferenceField>
                    <ReferenceField label="Breed" source='breed' reference="dog_breeds">
                        <TextField source="name" />
                    </ReferenceField>
                    <SelectField source="sex" choices={[
   { id: 'M', name: 'Male' },
   { id: 'F', name: 'Female' },
]} />
                    <BooleanField source="desexed" label='Desexed' />
                    <EmailField source="vet_email" label='Vet Email' />
                    <TextField source="vet" label='Vet' />
                    <DateField source="kc_due" label='KC Due' />
                    <DateField source="c3_due" label='C3 Due' />
                    <DateField source="c5_due" label='C5 Due' />
                    <EditButton />
                    </CustomizableDatagrid>
 
            )}
    
        </List>
    );
}
const PostTitle = ({ record }) => {
       return <span>Dog {record ? `"${record.dog_name}"` : ''}</span>;
    }

export const DogEdit = props => (
    <Edit title={<PostTitle />} {...props} >
        <TabbedForm>
                 <FormTab label="Dog Details">
        <TextInput source="dog_name" label='Dog Name' />
        <ReferenceInput  filterToQuery={searchText => ({ name: searchText })} source="owner" reference="clients"><AutocompleteInput optionText="name" /></ReferenceInput>
        <ReferenceInput  filterToQuery={searchText => ({ name: searchText })} source="breed" reference="dog_breeds"><AutocompleteInput optionValue="id" optionText="name" /></ReferenceInput>
        
          <SelectInput source='sex'  choices={[
   { id: 'M', name: 'Male' },
   { id: 'F', name: 'Female' },
]} label='Sex'/><BooleanInput
source="desexed"
options={{
    checkedIcon: <FavoriteIcon />,
}}
/>
</FormTab>
<FormTab label="Medical Details">
<TextInput source="vet" label='Vet' />
<TextInput source="vet_email" label='Vet Email' />
<DateInput source="kc_due" label='KC Due' options={{ format: 'DD/MM/YYYY' }} />
<DateInput source="c3_due" label='C3 Due' options={{ format: 'DD/MM/YYYY' }} />
<DateInput source="c5_due" label='C5 Due' options={{ format: 'DD/MM/YYYY' }} />
</FormTab>
</TabbedForm>
    </Edit>
);



export const DogCreate = props => (
      <Create {...props}>
                 <TabbedForm>
                 <FormTab label="Dog Details">
           <ReferenceInput  filterToQuery={searchText => ({ name: searchText })} source="owner" reference="clients"><AutocompleteInput optionText="name" /></ReferenceInput>
            
               <TextInput source="dog_name" label='Dog Name' />
               <TextInput  source="breed" label='Breed' />
               <SelectInput source='sex' choices={[
   { id: 'M', name: 'Male' },
   { id: 'F', name: 'Female' },
]} label='Sex'/>
<BooleanInput
    source="desexed"
    options={{
        checkedIcon: <FavoriteIcon />,
    }}
/>
</FormTab>
<FormTab label="Medical Details">
<TextInput source="vet" label='Vet' />
<TextInput source="vet_email" label='Vet Email' />
<DateInput source="kc_due" label='KC Due' options={{ format: 'DD/MM/YYYY' }} />
<DateInput source="c3_due" label='C3 Due' options={{ format: 'DD/MM/YYYY' }} />
<DateInput source="c5_due" label='C5 Due' options={{ format: 'DD/MM/YYYY' }} />
</FormTab>
</TabbedForm>
       </Create>
    
    );
