import * as React from "react";
import { List, Datagrid,Edit ,Pagination,     EditButton ,SimpleList, TextField, EmailField, TextInput, Create, SimpleForm} from 'react-admin';
import MyUrlField from '../fields/MyUrlField';
import { useMediaQuery } from '@material-ui/core';
import CustomizableDatagrid from 'ra-customizable-datagrid';

const PostPagination = props => <Pagination rowsPerPageOptions={[50, 100, 200]} {...props} />;



export const UserList = props =>{
    const isSmall = useMediaQuery(theme => theme.breakpoints.down('sm'));
   return (
    <List  perPage={50} {...props} pagination={<PostPagination />} >{isSmall ? (
        <SimpleList
            primaryText={record => record.name}
            secondaryText={record => record.email}
            tertiaryText={record => record.phone}
        />
    ) : (
    
      
    <CustomizableDatagrid>
            <TextField source="name" />
            <EmailField source="email" />
            <TextField source="phone" />
            <TextField source="instagram" />
         <EditButton/>
         </CustomizableDatagrid>
  
       )}
    </List>
);}

const PostTitle = ({ record }) => {
    return <span>Client {record ? `"${record.name}"` : ''}</span>;
 }

export const UserCreate = props => (
    <Create {...props}>
         <SimpleForm>
             <TextInput source="name" label='Name' />
             <TextInput source="phone" label="Phone" />
             <TextInput source="email" label="Email" />
             <TextInput source="instagram" label="Instagram" />
         </SimpleForm>
     </Create>
  );

  export const UserEdit = props => (
    <Edit title={<PostTitle />} {...props} >
        <SimpleForm>
        <TextInput source="name" label='Name' />
             <TextInput source="phone" label="Phone" />
             <TextInput source="email" label="Email" />
             <TextInput source="instagram" label="Instagram" />
        </SimpleForm>
    </Edit>
);